/*
    min-height is useful when there is nothing loaded in html, but we still can allow body occupy some spaces, so something like background
    will be visible
*/
html, body{
    height: 100%;
    min-height: 100vh;
    box-sizing: border-box;
}
/*
    height: calc(900vw / 16);
*/
@media all and (min-width: 1024px){
    html, body{
        height: 66em;
    }
}
body{
    margin: 0;
    overflow-x: hidden;
    overflow-y: auto;
    min-width: 320px;
}

:root{
    --font-size-tiny: 0.6rem;
    --font-size-smallest: 0.7rem;
    --font-size-smaller: 0.8rem;
    --font-size-small: 0.9rem;
    --font-size-normal: 1rem;
    --font-size-large: 1.25rem;
    --font-size-larger: 1.5rem;
    --font-size-largest: 1.75rem;
    --font-size-extra: 2rem;
}
:root{
    /* Blue sets */
    --color-blue-light: rgb(90, 177, 252);
    --color-blue-normal: rgb(21, 61, 232);
    --color-blue-dark: rgb(17, 42, 149);
    --color-blue-darkest: rgb(5, 14, 79);

    /* Gold/Yellow sets */
    --color-gold-light:rgb(236, 231, 88);
    --color-gold-normal: rgb(214, 176, 116);
    --color-gold-dark: rgb(154, 148, 124);

    /* White sets */
    --color-white-normal: rgb(246, 246, 246);
    --color-white-dark: rgb(224, 224, 224);
    --color-white-darkest: rgb(184, 184, 184);

    /* Grey sets */
    --color-grey-normal: rgb(154, 154, 154);
    --color-grey-dark: rgb(124, 124, 124);
    --color-grey-darkest: rgb(85, 85, 85);

    /* Black sets */
    --color-black-normal: rgb(55, 55, 55);
    --color-black-dark: rgb(21, 21, 21);

    /*Other colors*/
    --color-red-normal: rgb(232, 21, 93);
    --color-green-normal: rgb(21, 232, 97);
    --color-egg-normal: rgb(230, 226, 211);
}
body *{
    margin: 0;
    text-decoration: none;
    box-sizing: border-box !important;
    line-height: 1.5 !important;
    -webkit-tap-highlight-color: transparent;
    outline: none !important;
}
p{
    text-align: center;
}
a{
    color: inherit;
}
input[type='number'] {
    -moz-appearance:textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
@-webkit-keyframes FadeIn{
    0%{opacity: 0;} 100%{opacity: 1;}
}
@keyframes FadeIn{
    0%{opacity: 0;} 100%{opacity: 1;}
}
button{
    border-radius: 0.125rem;
    padding: 1.25rem;
    box-shadow: 0 1rem 2rem 0.1rem rgba(21, 21, 21, 0.2);
    text-transform: uppercase;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    letter-spacing: 0.125rem;
    -webkit-transition: 1s;
    transition: 1s;
}
button:hover{
    cursor: pointer;
}
button:focus{
    outline: none;
}
button.button-1{
    background-color: var(--color-blue-normal);
    border: 0.5px solid var(--color-blue-normal);
    color: var(--color-white-normal);
}
button.button-1:hover{
    background-color: var(--color-blue-dark);
    border: 0.5px solid var(--color-blue-dark);
}
button.button-2{
    background-color: var(--color-white-normal);
    border: 0.5px solid var(--color-white-normal);
    color: var(--color-black-dark);
}
button.button-2:hover{
    background-color: var(--color-white-dark);
    border: 0.5px solid var(--color-white-dark);
}
form{
    width: 100%;
}
input{
    font-size: inherit;
}
form>div.Inline-Input{
    margin: auto;
    position: relative;
}
form>div.Inline-Input>input{
    width: 100%;  
    padding: 0.75rem 4rem 0.75rem 1.25rem;
    background-color: transparent;
} 
form>div.Inline-Input>label{
    position: absolute;
    right: 1rem;
    top: 0.75rem;
}
form>div.Inline-Input>p{
    opacity: 0;
    text-align: left;
    font-size: var(--font-size-small);
    padding-left: 1.25rem;
    color: var(--color-red-normal);
    -webkit-transition: 1s;
    transition: 1s;
}
form>div.Inline-Input>input.Checked:invalid~p{
    opacity: 1;
}
input[type='text'],input[type='password'],input[type='email'],input[type='number'],input[type='number']{
    border: 0;
    border-bottom: 1px solid var(--color-black-normal);
    outline: none;
}
input::-webkit-input-placeholder{
    color: var(--color-white-darkest);
}
input::-ms-input-placeholder{
    color: var(--color-white-darkest);
}
input::placeholder{
    color: var(--color-white-darkest);
}

input[type='text'].Checked:invalid, input[type='text'].Checked:invalid+label,
input[type='password'].Checked:invalid, input[type='password'].Checked:invalid+label,
input[type='email'].Checked:invalid, input[type='email'].Checked:invalid+label,
input[type='date'].Checked:invalid, input[type='date'].Checked:invalid+label,
input[type='number'].Checked:invalid, input[type='number'].Checked:invalid+label{
    color: var(--color-red-normal);
    border-bottom-color: var(--color-red-normal);
}

body{
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    font-size: var(--font-size-normal);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: relative;
}
#root{
    width: 100%;
    height: 100%;
    position: relative;
}


/* Browsers' Default Styling Fixing */
@-webkit-keyframes autofill {
    to {
        color: inherit;
        background: transparent;
    }
}
input:-webkit-autofill {
    -webkit-animation-name: autofill;
    -webkit-animation-fill-mode: both;
}
select {
    /* for Firefox */
    -moz-appearance: none;
    /* for Chrome */
    -webkit-appearance: none;
  }

  /* For IE10 */
  select::-ms-expand {
    display: none;
  }

#Compatibility{
    width: 100%;
    height: 100%;
    background:  -webkit-linear-gradient(rgb(246, 246, 246) 0%, rgb(224, 224, 224) 100%);
    background:  linear-gradient(rgb(246, 246, 246) 0%, rgb(224, 224, 224) 100%);
    color: rgb(21, 21, 21);
    padding: 1.5rem 3rem;
}
#Compatibility>img{
    height: 3rem;
    display: block;
    margin: 0.5rem auto;
}
#Compatibility>p.Title{
    font-size: 1.75rem;
    margin-bottom: 1rem;
}
#Compatibility>div.Content>p.Description{
    color: #7c7c7c;
}
#Compatibility>div.Content>div.List{
    width: 100%;
    max-width: 24rem;
    margin: 1.5rem auto;
    margin-bottom: 1.5rem;
}
#Compatibility>div.Content>div.List>p{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0.75rem;
}
#Compatibility>div.Content>div.List>button{
    margin-top: 1.75rem;
}

/*The following are for sake of incompatible browers which does not support var */

#Compatibility>div.Content>div.List>button{
    font-size: 0.7rem;
    box-shadow: 0 0.5rem 2rem 0 rgb(184, 184, 184);
    background-color: rgb(21, 61, 232);
    border: 0.5px solid rgb(21, 61, 232);
    color: rgb(246, 246, 246);
}

div.LoadingScreen{
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-super);
    font-weight: 400;
    font-size: var(--font-size-large);
    z-index: 2;
    background: -webkit-linear-gradient(var(--color-white-normal), var(--color-white-dark));
    background: linear-gradient(var(--color-white-normal), var(--color-white-dark));
    color: var(--color-red-normal);
}
div.LoadingScreen>span{
    margin-right: 1.5rem;
}
div.LoadingScreen>div{
    display: flex;
    justify-content: center;
    align-items: center;
}


